* {
  box-sizing: border-box;
  font-size: #000; }

.clear-fix::after {
  content: '';
  display: block;
  clear: both; }

body {
  border-top: 3px solid #f8b910;
  font-family: 'OpenSans',sans-serif; }
  @media (min-width: 1349px) {
    body {
      width: 1349px; } }
  @media (max-width: 1920px) {
    body {
      width: 100%; } }

h1 {
  font-size: 20px;
  line-height: 14px;
  padding: 10px 0;
  margin: 20px 0; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.container {
  width: 980px;
  margin: auto; }
  @media (max-width: 768px) {
    .container {
      width: 100%;
      padding: 0 15px; } }

.button-link {
  font-family: "OpenSans-Bold", sans-serif;
