header .nav-wrapper {
  padding: 10px 0;
  border: 1px solid #ebebeb;
  background-color: #fafafa; }
  header .nav-wrapper nav ul.nav-list > li {
    position: relative;
    margin-right: 40px; }
    header .nav-wrapper nav ul.nav-list > li a {
      color: #000;
      font-size: 12px;
      line-height: 12px;
      display: block;
      text-transform: uppercase; }
      header .nav-wrapper nav ul.nav-list > li a:hover, header .nav-wrapper nav ul.nav-list > li a:focus {
        color: #2a9729; }
    header .nav-wrapper nav ul.nav-list > li .title-sub-menu {
      padding: 10px 0; }
    header .nav-wrapper nav ul.nav-list > li:not(:first-child):before {
      content: "";
      display: block;
      height: 3px;
      width: 3px;
      border-radius: 50%;
      background-color: #2a9729;
      margin-right: 20px;
      position: absolute;
      top: 50%;
      left: -20px;
      transform: translateY(-50%); }
    header .nav-wrapper nav ul.nav-list > li .sub-menu {
      position: absolute;
      top: 100%;
      left: 0;
      border-left: 3px solid #2a9729;
      max-height: 0;
      transition: 1s all ease;
      overflow: hidden;
      min-width: 150px;
      background-color: #fafafa;
      z-index: 2; }
      header .nav-wrapper nav ul.nav-list > li .sub-menu li {
        padding: 10px 0;
        transition: 0.3s all; }
        header .nav-wrapper nav ul.nav-list > li .sub-menu li a {
          padding-left: 13px;
          font-size: 14px;
          text-transform: none;
          display: block; }
        header .nav-wrapper nav ul.nav-list > li .sub-menu li:hover {
          background-color: #2a9729; }
          header .nav-wrapper nav ul.nav-list > li .sub-menu li:hover a {
            color: #fff; }
    header .nav-wrapper nav ul.nav-list > li:hover {
      overflow: visible; }
      header .nav-wrapper nav ul.nav-list > li:hover > a {
        color: #2a9729; }
      header .nav-wrapper nav ul.nav-list > li:hover > .sub-menu {
        max-height: 300px; }
  header .nav-wrapper .search form {
    border: 1px solid #ebebeb;
    padding: 12px 15px;
    color: #aaaaaa; }
    header .nav-wrapper .search form input[type="text"] {
      width: 100%;
      border: none; }
    header .nav-wrapper .search form input:focus {
      outline: none; }
    header .nav-wrapper .search form .search-input {
      position: relative;
      width: 270px; }
      header .nav-wrapper .search form .search-input .search-submit {
        position: absolute;
        top: 0;
        right: 0; }
        header .nav-wrapper .search form .search-input .search-submit input[type="submit"] {
          border: none;
          background: transparent;
          position: absolute;
          right: 0;
          cursor: pointer; }
        header .nav-wrapper .search form .search-input .search-submit i {
          position: absolute;
          right: 0; }

header .slider-w {
  margin-top: 30px;
