  .product:hover, .product:focus {
    border-color: #e5e5e5;
    box-shadow: 0 0 5px 1px #e5e5e5; }
    .product:hover a, .product:focus a {
      color: #2a9729; }
    .product:hover .button-link, .product:focus .button-link {
      color: #fff;
      background-color: #2a9729; }
